.All_Categorias {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: #ffaf4d;
  padding: 10px;
  font-size: 18px;
  height: 100%;
  transition: all 1s ease;
  grid-area: Categoria; }
  .All_Categorias .All_Categorias_title {
    margin-left: 3px; }
  .All_Categorias :nth-child(3) {
    margin: 0 0 0 auto; }
  .All_Categorias:hover {
    color: #b36300;
    fill: #b36300; }
  .All_Categorias .StyledListCategorias {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    box-sizing: border-box;
    background: #fff;
    transition: transform 0.22s ease-in;
    border: 2px solid #ff8d00;
    z-index: 200; }
    .All_Categorias .StyledListCategorias.close {
      transform: translateX(-300px); }
    .All_Categorias .StyledListCategorias li {
      padding: 0 15px; }
      .All_Categorias .StyledListCategorias li a {
        display: block;
        width: 100%;
        transition: all 0.5s;
        background-color: transparent;
        font-size: 18px;
        cursor: pointer;
        color: #222;
        border-bottom: 1px solid #eee; }
        .All_Categorias .StyledListCategorias li a:hover {
          color: #ffd400;
          background: #eee; }
    .All_Categorias .StyledListCategorias .manuListDropHover {
      position: relative; }
      .All_Categorias .StyledListCategorias .manuListDropHover a {
        border-bottom: none; }
        .All_Categorias .StyledListCategorias .manuListDropHover a #CategoriaSVG {
          margin-right: 10px;
          fill: black; }
      .All_Categorias .StyledListCategorias .manuListDropHover .manuDropList {
        position: absolute;
        left: 100%;
        opacity: 0;
        top: -9999em;
        transition: 0.5s opacity;
        border-left: 10px solid transparent;
        background: #fff;
        border: none;
        display: flex;
        justify-content: space-around;
        cursor: default; }
        .All_Categorias .StyledListCategorias .manuListDropHover .manuDropList h3 {
          border-bottom: 1px solid #ff8d00; }
      .All_Categorias .StyledListCategorias .manuListDropHover:hover .manuDropList {
        opacity: 1;
        top: 0; }
      .All_Categorias .StyledListCategorias .manuListDropHover::after {
        content: " ";
        position: absolute;
        border: 5px solid;
        border-color: transparent transparent transparent #363d36;
        top: 33.333%;
        right: 0;
        transition: 0.5s; }
      .All_Categorias .StyledListCategorias .manuListDropHover:hover::after {
        transform: scaleX(-1); }
