.Button {
  border-radius: 4px;
  line-height: 1em;
  border: none;
  outline: none;
  cursor: pointer;
  font-weight: 500; }
  .Button.grey {
    background-color: #F5F6F7;
    color: #404144; }
    .Button.grey:hover {
      background-color: #EF6722;
      color: #fff; }
  .Button.orange {
    background-color: #EF6722;
    color: #fff; }
    .Button.orange:hover {
      background-color: #222426; }
  .Button.orange-light {
    background-color: #F9EDE7;
    color: #EF6722; }
  .Button.outline {
    background-color: white;
    color: #222426;
    box-shadow: inset 0 0 0 2px #EBECED;
    transition: 0.3s ease all; }
    .Button.outline:hover {
      box-shadow: inset 0 0 0 2px #EF6722;
      background-color: #EF6722;
      color: #fff; }
  .Button.transparent {
    background-color: transparent;
    transition: 0.3s ease all; }
    .Button.transparent:hover {
      color: #404144; }
      .Button.transparent:hover span {
        color: #404144; }
  .Button.white {
    background-color: #fff;
    color: #EF6722; }
    .Button.white:hover {
      background-color: #EF6722;
      color: white; }
  .Button.md {
    padding: 15px 16px; }
  .Button.sm {
    padding: 11px 12px; }
  .Button.xs {
    padding: 8px 12px; }
