#basket {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  z-index: 100;
  width: max-content;
  height: 100%; }
  #basket .SvgIcon {
    width: 100%;
    height: 100%; }
  #basket:hover .SvgIcon {
    fill: #EF6722; }
  #basket:hover::after {
    background-color: #EF6722 !important;
    border: 2px solid #EF6722 !important; }
  #basket::after {
    z-index: 200; }

.HoverShow_Element {
  right: 5% !important; }
