.ChatIcon {
  display: inline-block;
  position: fixed;
  bottom: 10%;
  right: 50px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #EF6722;
  z-index: 999; }
  .ChatIcon a {
    background: url("smile-icon.svg") no-repeat center center;
    display: block;
    width: 100%;
    height: 100%;
    transition: .2s ease all; }
    .ChatIcon a:hover {
      transform: scale(1.2); }
