.HoverShow {
  position: relative;
  width: max-content;
  z-index: 100; }
  .HoverShow_Element {
    position: absolute;
    opacity: 0;
    transition: 0.2s ease all;
    box-shadow: 0 6px 24px rgba(107, 108, 111, 0.16), 0 0 2px rgba(107, 108, 111, 0.24);
    z-index: 100;
    visibility: hidden;
    width: max-content; }
    .HoverShow_Element._d {
      top: 80%;
      right: 0; }
    .HoverShow_Element._r {
      top: 0;
      left: 80%; }
  .HoverShow.opened::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    bottom: -20px;
    height: 120%;
    width: 150%;
    z-index: 0; }
  .HoverShow.opened .HoverShow_Element {
    display: block;
    visibility: visible;
    position: absolute;
    opacity: 1;
    z-index: 100;
    width: max-content; }
    .HoverShow.opened .HoverShow_Element._d {
      top: 110%;
      right: 0; }
    .HoverShow.opened .HoverShow_Element._r {
      top: 0;
      left: 105%; }
      .HoverShow.opened .HoverShow_Element._r::before {
        content: "";
        position: absolute;
        top: 0;
        left: -20%;
        bottom: -20px;
        height: 100%;
        width: 20%;
        z-index: -1; }
    .HoverShow.opened .HoverShow_Element_Component {
      color: black; }
