.Header .Navigation {
  display: flex;
  align-items: center; }
  .Header .Navigation.mobile {
    width: 100%; }
  .Header .Navigation .Navigation_list {
    display: flex;
    line-height: 40px;
    transition: 0.3s ease all; }
    .Header .Navigation .Navigation_list.stripped {
      flex: 1;
      justify-content: flex-end; }
      .Header .Navigation .Navigation_list.stripped .navigation-item {
        margin-left: 4vw; }
        .Header .Navigation .Navigation_list.stripped .navigation-item .Link {
          display: flex;
          align-items: center;
          justify-content: center;
          color: #F9EDE7;
          border: 1px solid #F9EDE7;
          padding: 7px;
          transition: 0.3s ease all;
          border-radius: 5px;
          width: 120px;
          line-height: normal; }
          .Header .Navigation .Navigation_list.stripped .navigation-item .Link:hover {
            background-color: #F9EDE7;
            color: #fe7e0f;
            border-radius: 2px; }
          @media (max-width: 347px) {
            .Header .Navigation .Navigation_list.stripped .navigation-item .Link {
              font-size: 9px; } }
        @media (max-width: 991.98px) {
          .Header .Navigation .Navigation_list.stripped .navigation-item {
            margin-left: 2vw;
            margin-right: 1vw;
            width: 110px;
            flex: 1; } }
        @media (max-width: 767.98px) {
          .Header .Navigation .Navigation_list.stripped .navigation-item {
            margin-left: 5px;
            margin-right: 0;
            font-size: 10px; }
            .Header .Navigation .Navigation_list.stripped .navigation-item .Link {
              width: auto;
              padding: 5px 1px; }
            .Header .Navigation .Navigation_list.stripped .navigation-item:first-child {
              margin-left: 0; }
            .Header .Navigation .Navigation_list.stripped .navigation-item:last-child {
              margin-right: 0; } }
      @media (max-width: 767.98px) {
        .Header .Navigation .Navigation_list.stripped {
          flex-direction: row;
          margin-top: 15px;
          width: 100%; } }
    .Header .Navigation .Navigation_list.authorization_buttons .navigation-item {
      margin-left: 32px; }
      .Header .Navigation .Navigation_list.authorization_buttons .navigation-item .sign_btn {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #F9EDE7;
        border: 1px solid #F9EDE7;
        background-color: #EF6722;
        padding: 7px;
        transition: 0.3s ease all;
        border-radius: 5px;
        width: 80px;
        line-height: normal; }
        .Header .Navigation .Navigation_list.authorization_buttons .navigation-item .sign_btn:hover {
          background-color: #F9EDE7;
          color: #fe7e0f;
          border-radius: 2px; }
    @media (max-width: 991.98px) {
      .Header .Navigation .Navigation_list.authorization_buttons .navigation-item {
        margin-left: 12px; } }
    @media (max-width: 767.98px) {
      .Header .Navigation .Navigation_list.authorization_buttons {
        display: none; } }

.Header a {
  font-weight: 500; }
  .Header a.sign-in {
    font-weight: 600; }

.Header_access-private {
  padding-top: 24px;
  padding-bottom: 10px;
  color: #222426;
  background-color: #ffffff;
  box-shadow: 0 1px 2px rgba(137, 138, 140, 0.2); }
  .Header_access-private a:hover {
    color: #EF6722; }
  @media (max-width: 767.98px) {
    .Header_access-private .Navigation {
      display: flex;
      flex-direction: column; } }

.Header_access-public {
  padding: 17px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  background-color: #fe7e0f; }

@media (max-width: 767.98px) {
  .Header {
    flex-direction: column; }
    .Header .Logo {
      display: block; } }

.Concept_access-public {
  border-top: 3px solid #8b00a5;
