.Input {
  display: flex;
  position: relative; }
  .Input input {
    width: 100%;
    border: none;
    border-radius: 4px;
    outline: none;
    color: #404144;
    font-weight: 500;
    background-color: #F5F6F7;
    transition: 0.2s ease all; }
    .Input input::placeholder {
      color: #C5C6C7; }
    .Input input:active, .Input input:focus {
      box-shadow: inset 0 0 0 2px #EF6722; }
    .Form_private .Input input {
      background-color: #fff;
      border: 1px solid #EBECED; }
  .Input.invalid input {
    border: 2px solid #DC3532; }
    .Input.invalid input::placeholder {
      color: #222426; }
    .Input.invalid input:focus {
      box-shadow: none; }
  .Input.disabled input {
    background-color: #EBECED;
    border: 2px solid #C5C6C7;
    opacity: 0.6;
    pointer-events: none; }
    .Input.disabled input::placeholder {
      color: #A7A8AA; }
    .Input.disabled input:focus {
      box-shadow: none; }
  .Input.label-right input {
    border-radius: 4px 0 0 4px; }
  .Input.label-right .label-right {
    border-radius: 0 4px 4px 0; }
  .Input.lg_m input {
    font-size: 16px;
    line-height: 16px;
    padding: 10px 20px;
    width: 50vw; }
  .Input.lg_m.icon-right input {
    padding-right: 40px; }
  .Input.lg_m.icon-right .SvgIcon {
    width: 15px;
    height: 15px;
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translate(0, -50%); }
    .Input.lg_m.icon-right .SvgIcon path {
      fill: #404144; }
  .Input.lg input {
    font-size: 16px;
    line-height: 16px;
    padding: 20px; }
  .Input.lg .label-left,
  .Input.lg .label-right {
    padding: 21px 16px; }
  .Input.lg.icon-right input {
    padding-right: 40px; }
  .Input.lg.icon-right .SvgIcon {
    width: 15px;
    height: 15px;
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translate(0, -50%); }
    .Input.lg.icon-right .SvgIcon path {
      fill: #404144; }
  .Input.md input {
    font-size: 1em;
    line-height: 1em;
    padding: 15px 16px; }
    .Input.md input:active, .Input.md input:focus {
      box-shadow: inset 0 0 0 2px #EF6722; }
  .Input.sm input {
    font-size: 1em;
    line-height: 1em;
    padding: 11px 12px;
    box-shadow: inset 0 0 0 1px #d9dadb; }
    .Input.sm input:active, .Input.sm input:focus {
      box-shadow: inset 0 0 0 1px #EF6722; }
  .Input .label-left,
  .Input .label-right {
    color: #6B6C6F;
    background-color: #EBECED;
    font-weight: 500; }
