.SelectCategories {
  position: relative; }
  .SelectCategories .options .scrollArea > * {
    overflow: hidden !important; }
  .SelectCategories .options_List {
    overflow-y: inherit; }
  .SelectCategories.opened svg.SvgIcon {
    transform: translate(-50%, -50%) rotate(180deg); }
  .SelectCategories .selected,
  .SelectCategories .SelectOption {
    height: 100%;
    border-radius: 4px;
    outline: none;
    color: #404144;
    font-weight: 500;
    position: relative;
    background-color: #F5F6F7;
    cursor: pointer;
    border: 2px solid transparent;
    transition: 0.2s ease all; }
    .SelectCategories .selected .SvgIcon,
    .SelectCategories .SelectOption .SvgIcon {
      position: absolute;
      width: 10px;
      height: 5px;
      right: 12px;
      fill: #898A8C;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: 0.2s ease all; }
  .SelectCategories.invalid .selected {
    border-color: transparent;
    border: 2px solid #DC3532; }
    .SelectCategories.invalid .selected::placeholder {
      color: #222426; }
    .SelectCategories.invalid .selected:focus {
      box-shadow: none; }
  .SelectCategories.disabled .selected {
    border-color: transparent;
    background-color: #EBECED;
    border: 2px solid #C5C6C7;
    opacity: 0.6;
    pointer-events: none; }
    .SelectCategories.disabled .selected::placeholder {
      color: #A7A8AA; }
    .SelectCategories.disabled .selected:focus {
      box-shadow: none; }
  .SelectCategories.lg .selected,
  .SelectCategories.lg .SelectOption {
    font-size: 16px;
    line-height: 16px;
    padding: 18px 40px 18px 20px; }
  .SelectCategories.md .selected,
  .SelectCategories.md .SelectOption {
    font-size: 1em;
    line-height: 1em;
    padding: 15px 16px; }
  .SelectCategories.sm .selected,
  .SelectCategories.sm .SelectOption {
    font-size: 1em;
    line-height: 1em;
    padding: 11px 12px; }
  .SelectCategories .SelectOption {
    min-width: 100%;
    background-color: #fff;
    border: none; }
    .SelectCategories .SelectOption:hover {
      background-color: #F5F6F7; }
      .SelectCategories .SelectOption:hover .SvgIcon {
        transform: translate(-50%, -50%); }
    .SelectCategories .SelectOption_List {
      background-color: #ffffff;
      position: absolute;
      top: 0;
      transition: 0.2s ease all;
      left: 101%;
      min-height: 100%;
      min-width: 100%;
      display: flex;
      box-shadow: 0 6px 24px rgba(107, 108, 111, 0.16), 0 0 2px rgba(107, 108, 111, 0.24);
      border-radius: 4px;
      justify-content: space-around;
      padding: 10px; }
      .SelectCategories .SelectOption_List:hover {
        background-color: #F5F6F7; }
      .SelectCategories .SelectOption_List_Block {
        margin-right: 10px;
        z-index: 100; }
        .SelectCategories .SelectOption_List_Block_Subsec {
          justify-content: flex-end;
          padding: 5px;
          margin-left: 10px; }
          .SelectCategories .SelectOption_List_Block_Subsec li {
            margin-bottom: 7px; }
            .SelectCategories .SelectOption_List_Block_Subsec li:hover {
              background-color: #C5C6C7; }
        .SelectCategories .SelectOption_List_Block_Title {
          justify-content: flex-start;
          padding: 5px;
          color: #898A8C; }
