.ClickShow {
  position: relative;
  width: max-content; }
  .ClickShow_Element {
    position: absolute;
    opacity: 0;
    top: 80%;
    z-index: -1;
    visibility: hidden;
    width: max-content;
    transition: 0.2s ease all;
    box-shadow: 0 6px 24px rgba(107, 108, 111, 0.16), 0 0 2px rgba(107, 108, 111, 0.24); }
  .ClickShow.opened .ClickShow_Element {
    display: block;
    position: absolute;
    opacity: 1;
    z-index: 100;
    top: 110%;
    width: max-content;
    color: black;
    visibility: visible; }
    .ClickShow.opened .ClickShow_Element_Component {
      color: black; }
