.Footer .Logo {
  margin-bottom: 29px; }

.Footer .copyright {
  color: #6B6C6F; }

.Footer h5 {
  color: #EF6722;
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
  margin: 18px 0 30px; }

.Footer .flex-list {
  display: flex; }

.Footer ul:not(.SocialList) li {
  font-weight: 500; }
  .Footer ul:not(.SocialList) li a:hover {
    color: #EF6722; }
  .Footer ul:not(.SocialList) li + li {
    margin-top: 10px; }

.Footer_access-private {
  padding-top: 45px;
  padding-bottom: 34px;
  color: #A7A8AA;
  background-color: #222426; }
  .Footer_access-private .list-item {
    flex: 1 0 25%; }
  .Footer_access-private .SocialList {
    display: flex;
    justify-content: flex-end; }
    .Footer_access-private .SocialList .navigation-item:not(:first-child) {
      margin-left: 16px; }
    .Footer_access-private .SocialList .fab {
      font-size: 18px; }
  .Footer_access-private .copyright {
    margin: 0; }

.Footer_access-public {
  margin-top: 112px;
  padding: 78px 0 60px;
  background-color: #fe7e0f; }
  .Footer_access-public .SocialList {
    margin-top: 25px;
    margin-bottom: 30px; }
    .Footer_access-public .SocialList a {
      line-height: 40px;
      height: 40px;
      width: 40px;
      display: inline-block;
      background-color: #EBECED;
      color: #404144;
      border-radius: 50%;
      font-size: 16px; }
      .Footer_access-public .SocialList a:hover {
        color: white;
        background-color: #EF6722; }
    .Footer_access-public .SocialList li + li {
      margin-left: 16px; }
  .Footer_access-public .flex-list {
    justify-content: flex-start;
    padding-left: 11px; }
    .Footer_access-public .flex-list h5 {
      margin-top: 7px; }
    .Footer_access-public .flex-list .list-item + .list-item {
      margin-left: 120px; }
    @media (max-width: 400px) {
      .Footer_access-public .flex-list .list-item {
        width: 100%; } }

.SocialList li {
  text-align: center; }
