.Select.opened .selected {
  border-color: #EF6722;
  background-color: #ffff; }

.Select.opened svg.SvgIcon {
  transform: translate(-50%, -50%) rotate(180deg); }

.Select .selected,
.Select .SelectOption {
  border-radius: 4px;
  outline: none;
  color: #404144;
  font-weight: 500;
  position: relative;
  background-color: #F5F6F7;
  cursor: pointer;
  border: 2px solid transparent;
  transition: 0.2s ease all; }
  .Select .selected .SvgIcon,
  .Select .SelectOption .SvgIcon {
    position: absolute;
    width: 10px;
    height: 5px;
    right: 3px;
    top: 50%;
    fill: #898A8C;
    transform: translate(-50%, -50%);
    transition: 0.2s ease all; }

.Select.invalid .selected {
  border-color: transparent;
  border: 2px solid #DC3532; }
  .Select.invalid .selected::placeholder {
    color: #222426; }
  .Select.invalid .selected:focus {
    box-shadow: none; }

.Select.disabled .selected {
  border-color: transparent;
  background-color: #EBECED;
  border: 2px solid #C5C6C7;
  opacity: 0.6;
  pointer-events: none; }
  .Select.disabled .selected::placeholder {
    color: #A7A8AA; }
  .Select.disabled .selected:focus {
    box-shadow: none; }

.Select .SelectOption {
  background-color: #fff;
  border: none; }
  .Select .SelectOption:hover {
    background-color: #F5F6F7; }

.Select.lg .selected,
.Select.lg .SelectOption {
  font-size: 16px;
  line-height: 16px;
  padding: 18px 40px 18px 20px; }

.Select.lg_m .selected,
.Select.lg_m .SelectOption {
  font-size: 16px;
  line-height: 16px;
  padding: 18px;
  min-width: 10vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start; }

.Select.md .selected,
.Select.md .SelectOption {
  font-size: 1em;
  line-height: 1em;
  padding: 15px 16px; }

.Select.sm .selected,
.Select.sm .SelectOption {
  font-size: 1em;
  line-height: 1em;
  padding: 11px 12px; }

.Select .SelectOption {
  border-radius: 4px; }

.Select .ScrollArea > * {
  /* width */
  /* Track */
  /* Handle */ }
  .Select .ScrollArea > *::-webkit-scrollbar {
    width: 4px; }
  .Select .ScrollArea > *::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #C5C6C7; }
  .Select .ScrollArea > *::-webkit-scrollbar-thumb {
    background: #898A8C; }
